import { useWeb3React } from '@web3-react/core';
import { BigNumber, ethers } from 'ethers'
import React, {useState, useEffect} from 'react'
import {UncontrolledTooltip, Row, Col, Card, CardTitle, Alert, CardText, Input, Button, Badge, InputGroup, InputGroupAddon, InputGroupText, Progress} from 'reactstrap'
import { usePooledStakingETHContract } from '../../contracts/useContract';
function DepositTab() {
    const {account, library, chainId } = useWeb3React();

    const formatBN = (bn) => {
        return ethers.utils.commify(ethers.utils.formatEther(bn.toString()));
    }

    const [amountToDeposit, setAmountToDeposit] = useState(0);
    const [balanceSHEGIC, setBalanceSHEGIC] = useState(ethers.BigNumber.from('0'));
    const [statusMsg, setStatusMsg] = useState();
    const pooledStakingETH = usePooledStakingETHContract();
    const [numberOfStakingLots, setNumberOfStakingLots] = useState(ethers.BigNumber.from('0'));
    const [totalBalance, setTotalBalance] = useState(ethers.BigNumber.from('0'));
    const [lockedBalance, setLockedBalance] = useState(ethers.BigNumber.from('0'));
    const [sharesInLastLot, setSharesInLastLot] = useState(ethers.BigNumber.from('0'));
    const [availablePercentage, setAvailablePercentage] = useState(0);
    const [pendingPercentage, setPendingPercentage] = useState(0);
    const [tooltipOpen, setTooltipOpen] = useState(false);

    const toggle = () => setTooltipOpen(!tooltipOpen);
    useEffect(() => {
        if(!!account && !!library)
            pooledStakingETH.balanceOf(account).then((balance) => setBalanceSHEGIC(balance));
    }, [account, library, chainId, pooledStakingETH])

    useEffect(() => {
        if(!!account && !!library){
            pooledStakingETH.totalBalance().then((balance) => setTotalBalance(balance));
            pooledStakingETH.lockedBalance().then((balance) => setLockedBalance(balance));
            pooledStakingETH.numberOfStakingLots().then(n => {
                setNumberOfStakingLots(n)
                const STAKING_LOT_PRICE_DIV_100 = ethers.BigNumber.from('8880000000000000000000'); // divided by 100
                pooledStakingETH.getStakingLotShares(n, account)
                .then(shares => 
                    setSharesInLastLot(shares.div(STAKING_LOT_PRICE_DIV_100)));
            });
        }
     }, [account, library, chainId])

    useEffect(() => {
        if(!totalBalance.isZero()){
            const availablePercentage = totalBalance.sub(lockedBalance).mul(ethers.BigNumber.from('100')).div(ethers.utils.parseEther('888000')).toNumber();
            const pendingPercentage = 100 - availablePercentage;
            console.log("available: ", availablePercentage);
            console.log("pending: ", pendingPercentage);

            setAvailablePercentage(availablePercentage);
            setPendingPercentage(pendingPercentage);
        }

    }, [totalBalance, lockedBalance, numberOfStakingLots, sharesInLastLot])

    const waitAndUpdate = async (txRequest) => {
        console.log(txRequest.hash)
        setStatusMsg("Pending "+txRequest.hash);
        await txRequest.wait();
        setStatusMsg("");
    }

    const depositHegic = async () => {
        const txRequest = await pooledStakingETH.deposit(amountToDeposit)
        await waitAndUpdate(txRequest)
    }

    const StatusMsg = () => {
        return (
            <>
            { statusMsg ? (
                <Alert color="primary">
                    {statusMsg}
                </Alert> 
            ) : null }
            </>
        );
    }

    return (
        <Row>
        <Col sm="12">
            <Card body>
                <CardTitle><h3>Stake $HEGIC</h3></CardTitle>

                <CardText>
                    <Badge color="primary">You have {formatBN(balanceSHEGIC)} sHEGIC</Badge>
                </CardText>

                <CardText>
                    Deposit your $HEGIC in the pool and start earning fees generated by the Hegic Protocol.<br />
                    1. Deposit your $HEGIC<br />
                    2. When deposited amount reaches the Staking Lot Price, the contract will buy a Hegic Staking Lot<br />
                    3. Earn fees and exit the pool when you want
                </CardText>
                <CardText>
                    <h5>Next Lot</h5> 
                    <Badge>{ formatBN(ethers.utils.parseEther('888000').sub(totalBalance.sub(lockedBalance)).toString())} HEGIC until next Staking Lot purchase</Badge>
                    { totalBalance.toString() == '0' ? null : (
                    <div>
                        { sharesInLastLot.toString() == '0' ? (
                            <Progress multi>
                                <Progress bar color="warning" value={availablePercentage}>Others</Progress>
                                <Progress bar color="secondary" value={pendingPercentage}>Pending</Progress>
                            </Progress>
                        ) : (<>
                            <Progress multi>
                                <Progress bar id='progress-others' color="warning" value={availablePercentage-sharesInLastLot}>Others</Progress>
                                <Progress bar id='progress-you' color="primary" value={sharesInLastLot}>You</Progress>
                                <Progress bar id='progress-pending' color="secondary" value={pendingPercentage}>Pending</Progress>
                            </Progress>
                            <UncontrolledTooltip placement="bottom"  target="progress-others" >
                            {formatBN(totalBalance.sub(lockedBalance).sub(balanceSHEGIC))} HEGIC
                          </UncontrolledTooltip>
                            <UncontrolledTooltip placement="bottom" target="progress-you" >
                            {formatBN(balanceSHEGIC)} HEGIC
                          </UncontrolledTooltip>
                          <UncontrolledTooltip placement="bottom" target="progress-pending" >
                            {formatBN(ethers.utils.parseEther('888000').sub(totalBalance.sub(lockedBalance)))} HEGIC
                          </UncontrolledTooltip>
                          </>
                        ) }
                    </div>
                    )}
                </CardText>
                <InputGroup>
                    <Input placeHolder="0" onChange={(event) => setAmountToDeposit(ethers.utils.parseEther(event.target.value))}/>
                    <InputGroupAddon addonType="append">
                    <InputGroupText>HEGIC</InputGroupText>
                    </InputGroupAddon>  
                </InputGroup>
                <Button style={{
                        marginTop:'10px',
                        color:'#15203d',
                        fontWeight:'bold',
                        fontFamily:'Jura',
                        letterSpacing:'.1em',
                        background:'transparent',
                        borderImageSource:'url(https://www.hegic.co/assets/img/button-primary.svg)', 
                        borderImageSlice:'20',
                        borderStyle:'solid',
                        boxSizing:'border-box',
                        borderRadius:'2px',
                        borderImageWidth:'50px'}} onClick={depositHegic}><b>DEPOSIT</b></Button>
                <StatusMsg />
            </Card>
        </Col>
      </Row>
    )
}

export default DepositTab;